<template>
  <div>
    <!-- accordion_info_detail -->
    <div class="accordion_info_detail">
        <AccordionInfoDetailTitle></AccordionInfoDetailTitle>

        <b-list-group>
          <b-list-group-item href="#">
            <div class="d-flex w-100 justify-content-between">
              <h5 class="mb-1"></h5>
            </div>
            <p class="mb-1">
              <ul>
                <li>
                  <dl>
                    <dd>{{ myboard.daytraffic }}</dd>
                  </dl>
                </li>
              </ul>
            </p>
            <small>      .</small>
          </b-list-group-item>
          <b-list-group-item href="#">
            <div class="d-flex w-100 justify-content-between">
              <h5 class="mb-1"></h5>
              <!-- <strong class="text-muted"></strong> -->
            </div>
            <p class="mb-1">{{ myboard.dayfood }}</p>
          </b-list-group-item>
          <b-list-group-item href="#foobar">
            <div class="d-flex w-100 justify-content-between">
              <h5 class="mb-1"></h5>
              <small class="text-muted">23000</small>
            </div>
            <p class="mb-1">
              <ul>
                <li>
                  <dl>
                    <dt></dt>
                    <dd>5000</dd>
                  </dl>
                </li>
                <li>
                  <dl>
                    <dd>{{ myboard.daypay }}</dd>
                  </dl>
                </li>
              </ul>
            </p>
          </b-list-group-item>
        </b-list-group>
    </div>
  </div>
</template>

<script>
  import AccordionInfoDetailTitle from './accordionInfoDetailTitle.vue';
  export default {
    components: {
      AccordionInfoDetailTitle
    },

    created() {
      var id = this.$route.params.id;

      this.$http.get(`/api/regist/${id}`)
        .then((response) => {
          this.myboard = response.data
          // console.log(response.data)
        })
    },

    data() {
        return {
          myboard: {}
    }
    }
  }
</script>

<style>
  .accordion_info_detail {
    margin-top: 20px;
  }

  .list-group-item.active {
    background: #25333c;
    border-color: #25333c;
  }
</style>


