<template>
  <div class="join_wrapper">
    <table>
      <tr>
        <th> *</th>
        <td class="put"><input type="text" id="id" v-model="user.userid"></td>
      </tr>
      <tr>
        <th> *</th>
        <td class="put"><input type="password" id="password" v-model="user.password"></td>
      </tr>
      <tr>
        <th>  *</th>
        <td class="put"><input type="password" id="pwdcheked" name="pwdchecked"></td>
      </tr>
      <tr>
        <th> *</th>
        <td class="put"><input type="text" id="name" v-model="user.name"></td>
      </tr>
      <tr>
        <th> *</th>
        <td class="put"><input type="text" id="age" v-model="user.age"></td>
      </tr>
      <b-button class="joinBtn" v-on:click="signUp"></b-button>
    </table>
  </div>
</template>

<script>
  export default {
    data: function () {
      return {
        // component data user  .
        // user   , ,      .
        user: {
          userid: '',
          password: '',
          name: '',
          age: ''
        }
      }
    },

    // axios /api/users/signUp    (this.user) user  . 
    methods: {
      signUp: function (event) {
        this.$http.post('/api/users/signUp1',
            this.user
          )
          .then((res) => {
            // , /login  
            if (res.data.success == true) {
              alert(res.data.message);
              this.$router.push('/login')
            }
            // , error 
            else if (res.data.success == false) {
              alert(res.data.message);
            }
          })
          .catch(function (error) {
            alert(error)
          })
      }
    }
  }
</script>

<style>
  .join_wrapper {
    width: 100%;
    padding: 5.4rem 0;
    margin: 0 auto;
  }

  table {
    width: 60%;
    border: 0;
    border-spacing: 0;
    border-collapse: collapse;
    margin: 0 auto;
    position: relative;
  }

  tr {
    border: 1px solid #ccc;
    display: table-row;
    vertical-align: inherit;
    border-color: #cccccc;
    padding: 10px;
  }

  th {
    font-size: 9pt;
    background: #fbfafa;
    width: 120px;
  }

  td {
    padding: 10px 0 8px 10px;
    border-left: 1px solid #cccccc;
  }

  .put {
    width: 800px;
  }

  .put input {
      width: 95%;
      height: 100%;
      border: none;
  }

  .joinBtn {
    margin-top: 10px;
    position: absolute;
    right: 0;
  }
</style>


