const USER_ID = 'USER_ID';

// State()
//   .
const state = {
  userId: '',
};

// Getters
//   .     .
const getters = {
  userId(state) {
    return state.userId
  },
};

// Mutations ()
// State  . commit  .
const mutations = {
  [USER_ID](state, payload) {
    state.userId = payload;
  },
};

// Actions ()
//   .
// HTTP get  setTimeout       .
// Dispatch  .
const actions = {
  
};

export default {
  state,
  getters,
  mutations,
  actions
}



