import Vue from 'vue'
import Router from 'vue-router'
import dataManager from '@/util/data-manager.js';
import routes from './routes.js';

Vue.use(Router)

export const router = new Router({
  routes: routes,
  mode: 'history'
});

/**
 *    
 *    vuex 
 */
router.beforeEach((to, from, next) => {
  dataManager.loadDataFromCookie();
  next();
});


